.groupFormHeader {
    font-weight: 600;
    font-size: 20px;
    color: gray
}

.GroupForm {
    background-color: #fff;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 40%;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    /* margin: 0 auto; */
    margin-top: 0;
    margin-right: 0;
    position: absolute;
    right: 15px;
    z-index: 100;
    top: 40px;
}

.field {
    width: 100%;
}

.field input {
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-sizing: border-box;
    border-radius: 6px;
    width: 100%;
    height: 40px;
    margin-top: 20px;
    padding: 5px;
    font-size: 15px;
}

.field input:focus {
    outline: none;
}

.field button {
    width: 100%;
    margin-top: 20px;
    background: #ef7917;
    border-radius: 2px;
    color: #fff;
    font-size: 20px;
    padding: 9px;
    border: none;
    outline: none;
    border-radius: 10px;
    cursor: pointer;
}

.ContactForm .field button:focus {
    outline: none;
}


